.main-page {
  // position: absolute;
  // top: 0;
  background-image: url(../../img/desktop-bg-image.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  min-height: 880px;
  margin-top: -120px;
  margin-bottom: 102px;

  @media (max-width: $vp-770) {
    background-image: url(../../img/tablet-bg-image.jpg);
    background-size: 100%;

  }
  @media (max-width: $vp-770) {

  }
}
.main-page__container {
  // padding: 0 349px;

  // @media (max-width: $vp-1440) {
  //   padding: 0 149px;
  // }
}

.main-page__header {
  font-family: $font--roboto-bold;
  font-style: normal;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: $light-color;
  margin: 285px 0 0 85px;

  @media (max-width: $vp-1024) {
    font-size: 70px;
    margin-left: 30px;
  }

  @media (max-width: $vp-770) {
    font-size: 64px;
    line-height: 75px;
    margin: 0 0 0 26px;
    padding-top: 160px;
  }

  @media (max-width: $vp-320) {
    font-size: 36px;
    line-height: 42px;
  }
}

.main-page__paragraph {
  font-family: $font--pt-mono;
  font-style: normal;
  font-size: 22px;
  line-height: 155%;
  color: $light-color;
  margin: 32px 0 45px 85px;

  @media (max-width: $vp-1024) {
    margin-left: 30px;
  }

  @media (max-width: $vp-770) {

    margin: 0 0 56px 25px;
  }

  @media (max-width: $vp-320) {
    font-size: 14px;
  }
}

.main-page__button {
  font-family: $font--roboto-bold;
  font-style: normal;
  font-size: 18px;
  line-height: 21px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $light-color;
  padding: 17px 26px;
  background-color: $blue;
  border: none;
  border-radius: 3px;
  margin-left: 85px;

  @media (max-width: $vp-1024) {
    margin-left: 30px;
  }

  @media (max-width: $vp-770) {
    margin-left: 72px;
  }

  @media (max-width: $vp-320) {
    display: none;
  }
}

.main-page__button--mobile {
  display: none;
  font-family: $font--roboto-bold;
  font-style: normal;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $light-color;
  padding: 17px 26px;
  background-color: $blue;
  border: none;
  border-radius: 3px;

  @media (max-width: $vp-320) {
    display: block;
  }
}
